html {
  color: #212121;
  background-color: #fff;
  font-family: verdana, arial, sans-serif;
  font-size: 22px;
  line-height: 1.3;
}

turbo-frame {
  display: block;
}

input, textarea, select, button {
  font-size: 100%;
}

a {
  color: #ec005a;
}

.turbo-progress-bar {
  background-color: #ec005a;
}

.controls {
  gap: 30px;
  margin-bottom: 15px;
  display: flex;
}

.controls button, .controls label {
  cursor: pointer;
}

.controls label {
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 2px 10px;
}

.controls input {
  height: 16px;
  width: 16px;
}

.board {
  display: table;
}

.board div {
  display: table-row;
}

.board label {
  padding: 0 1px 1px 0;
  display: table-cell;
}

.board label:has(input[value="0,0"]) {
  position: relative;
}

.board label:has(input[value="0,0"]):after {
  content: "";
  height: 8px;
  pointer-events: none;
  width: 8px;
  z-index: 2dppx;
  background-color: #c00;
  border-radius: 8px;
  position: absolute;
  top: 0;
  left: 0;
  transform: translate3d(-4px, -4px, 0);
}

.board input {
  appearance: none;
  webkit-appearance: none;
  cursor: pointer;
  height: 25px;
  width: 25px;
  border: 1px solid #ccc;
  border-radius: 3px;
  margin: 0;
  display: block;
}

.board input:checked {
  background-color: #333;
  border: 1px solid #333;
}

.board input:hover:not(:checked) {
  background-color: #ff70c7;
  border: 1px solid #bb1e7e;
}

